import React from 'react'

const Item = (props) => {

    return (
        <li onClick={ ()=>{ props.getCoinDetails(props.dataItem.CoinInfo.Id)}}>
            {props.coinNumber}
            {props.dataItem.CoinInfo.Id}
            {props.dataItem.CoinInfo.Name}
            <img src={`https://www.cryptocompare.com${props.dataItem.CoinInfo.ImageUrl}`} />
        </li>
    )
}


export default Item